import React, { Component } from 'react'
import { NavBar,Icon,SearchBar,WhiteSpace} from 'antd-mobile';
import Car from './Carousel';
export default class Stroe extends Component {
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'white'}}
                    rightContent={[
                        <Icon key="0" type="search" style={{ color:'#3fcccb', marginRight: '16px' }} />,
                    ]}>商城
                </NavBar>
                <SearchBar placeholder="输入关键字搜索"/>
                <Car/>
                <img src="/shangcheng.jpg" width="100%"/>
                <WhiteSpace/>
                <img src='/shangcheng_04.jpg' width="100%"/>
            </div>
        )
    }
}
