import React, { Component } from 'react'
import { NavBar, Icon,} from 'antd-mobile';
import Car from './Carousel';
export default class AppHome extends Component {
    render() {
        let img = {
            marginTop:'10px',
            marginLeft:'2.5%'
        }
        return (
            <div>
                <div style = {{backgroundColor:'#eeeeee'}}>
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'white'}}
                    rightContent={[
                        <Icon key="0" type="search" style={{ color:'#3fcccb', marginRight: '16px' }} />,
                    ]}>住吧
                </NavBar>
                    <Car/>
                    <div>
                        <img src='/3_img.jpg' width='95%' style={img}/>
                    </div>
                    <div style={{marginTop:'20px',backgroundColor:'white'}}>
                        <span style={{fontWeight:'1000',textShadow:'2px 2px 2px 2px #3fcccb',color:'#3fcccb',fontSize:'24px'}}>|</span>
                        <span style={{fontSize:'18px',marginLeft:'5px'}}>热门推荐</span>
                        <img src = '/bottom.jpg' width='100%'/>
                    </div>
                </div>
            </div>
        )
    }
}